/* App.css - Styling for the Book to Speech React App - Forest Warmth Theme */

:root {
  --primary-color: #5e8c31; /* Forest green */
  --secondary-color: #9c5518; /* Warm brown */
  --text-color: #f8f4e3; /* Soft cream */
  --border-color: #7a6b48; /* Muted gold */
  --success-color: #7fb069; /* Light green */
  --error-color: #d76464; /* Soft red */
  --hover-color: #8aa857; /* Lighter green */
  --bg-color: #2c2915; /* Dark olive */
  --bg-secondary: #3a381c; /* Darker olive */
  --accent-color: #cc9c3a; /* Golden yellow */
  --highlight-color: #56733c; /* Moss green */
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  overflow-x: hidden;
  position: relative;
}

.App {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header Section */
.App-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  position: relative;
}

.App-header h1 {
  color: var(--text-color);
  font-size: 2.4rem;
  margin-bottom: 8px;
  font-weight: 500;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.4);
}

.App-header p {
  color: var(--text-color);
  opacity: 0.85;
  font-size: 1.1rem;
}

/* Mode Toggle Section */
.mode-toggle {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  gap: 12px;
}

.mode-button {
  background-color: var(--bg-secondary);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 10px 18px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 15px;
  transition: all 0.2s ease;
}

.mode-button:hover {
  background-color: var(--highlight-color);
}

.mode-button.active {
  background-color: var(--primary-color);
  border-color: var(--accent-color);
  font-weight: 500;
}

/* Main Content Area */
main {
  flex: 1;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
}

/* Text Entry Components */
.text-input-section, .creative-corner {
  margin-bottom: 30px;
}

h2 {
  color: var(--accent-color);
  margin-bottom: 15px;
  font-weight: 500;
  text-align: center;
  font-size: 1.8rem;
}

.info-text {
  text-align: center;
  margin-bottom: 20px;
  color: var(--text-color);
  opacity: 0.9;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.text-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

/* API Key Input */
.api-key-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  flex-wrap: wrap;
  gap: 10px;
}

.api-key-bar label {
  font-weight: 500;
  margin-right: 15px;
  color: var(--accent-color);
}

.api-key-controls {
  display: flex;
  flex: 1;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}

.api-key-input {
  padding: 10px;
  flex: 1;
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  color: var(--text-color);
  font-size: 15px;
  min-width: 200px;
}

.api-key-save, .api-key-help-btn, .clear-key-btn {
  background-color: var(--primary-color);
  color: var(--text-color);
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  white-space: nowrap;
}

.api-key-help-btn {
  background-color: var(--secondary-color);
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-weight: bold;
}

.api-key-save:hover, .api-key-help-btn:hover, .clear-key-btn:hover {
  background-color: var(--hover-color);
}

.api-key-save:disabled {
  background-color: #5a5a5a;
  cursor: not-allowed;
}

.api-key-info {
  margin-bottom: 20px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 5px;
  text-align: center;
}

.api-key-status {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  padding: 8px;
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  gap: 15px;
}

.help-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  background-color: var(--secondary-color);
  border-radius: 50%;
  text-align: center;
  line-height: 18px;
  color: var(--text-color);
  font-weight: bold;
}

/* Voice Selection Components */
.voice-options-section {
  margin-top: 30px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.voice-controls {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.voice-selector {
  flex: 1;
  min-width: 200px;
}

.voice-selector label {
  display: block;
  margin-bottom: 8px;
  color: var(--accent-color);
  font-weight: 500;
}

.voice-selector select {
  width: 100%;
  padding: 10px;
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  color: var(--text-color);
  font-size: 15px;
}

.instructions-input {
  flex: 2;
  min-width: 300px;
}

.instructions-input label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  color: var(--accent-color);
  font-weight: 500;
}

.guide-toggle-button {
  background-color: var(--accent-color);
  color: var(--bg-color);
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
}

.guide-toggle-button:hover {
  background-color: var(--text-color);
}

.voice-instructions-textarea {
  width: 100%;
  padding: 10px;
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  color: var(--text-color);
  font-size: 15px;
  resize: vertical;
}

/* Instructions Guide Component */
.instructions-guide {
  margin-bottom: 25px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
  max-height: 60vh;
  overflow-y: auto;
}

.guide-header {
  text-align: center;
  margin-bottom: 15px;
}

.guide-header h3 {
  color: var(--accent-color);
  margin-bottom: 5px;
  font-size: 1.4rem;
}

.guide-header p {
  color: var(--text-color);
  opacity: 0.8;
}

.guide-section {
  padding: 15px;
}

.guide-section p {
  margin-bottom: 15px;
  line-height: 1.5;
}

.guide-section h4 {
  color: var(--accent-color);
  margin: 25px 0 15px;
  font-size: 1.2rem;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 5px;
}

.instruction-categories {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.instruction-category {
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.instruction-category:hover {
  background-color: rgba(0, 0, 0, 0.4);
  transform: translateY(-2px);
}

.category-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.category-icon {
  font-size: 1.5rem;
}

.category-title {
  font-weight: 500;
  color: var(--accent-color);
}

.examples-list {
  list-style: none;
  margin-top: 10px;
}

.examples-list li {
  margin-bottom: 12px;
  padding: 8px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  position: relative;
  padding-bottom: 40px;
}

.try-button {
  background-color: var(--primary-color);
  color: var(--text-color);
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  position: absolute;
  bottom: 8px;
  right: 8px;
}

.try-button:hover {
  background-color: var(--hover-color);
}

.guide-tips {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  padding: 15px;
  margin-bottom: 20px;
}

.tips-list {
  list-style: none;
}

.tips-list li {
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
}

.tips-list li:before {
  content: "✓";
  color: var(--accent-color);
  position: absolute;
  left: 0;
  top: 0;
}

/* Speech Sections Display */
.sections-section {
  margin-top: 30px;
}

.sections-list {
  margin-top: 20px;
}

.section-item {
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid var(--border-color);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.section-header h3 {
  color: var(--accent-color);
  font-size: 1.2rem;
}

.listen-button {
  background-color: var(--primary-color);
  color: var(--text-color);
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.listen-button:hover {
  background-color: var(--hover-color);
}

.listen-button:disabled {
  background-color: #5a5a5a;
  cursor: wait;
}

.section-content {
  background-color: rgba(255, 255, 255, 0.05);
  padding: 15px;
  border-radius: 5px;
  max-height: 200px;
  overflow-y: auto;
}

/* Audio Player Styling */
.audio-player {
  width: 100%;
  margin: 20px 0;
  border-radius: 30px;
  background-color: var(--bg-secondary);
}

.audio-player-section {
  margin-top: 25px;
}

/* Button Group */
.button-group {
  display: flex;
  gap: 15px;
  margin-top: 20px;
  justify-content: center;
}

.preview-button, .process-button {
  background-color: var(--primary-color);
  color: var(--text-color);
  border: none;
  padding: 12px 24px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  min-width: 160px;
  transition: all 0.2s ease;
}

.preview-button:hover, .process-button:hover {
  background-color: var(--hover-color);
  transform: translateY(-2px);
}

.preview-button:disabled, .process-button:disabled {
  background-color: #5a5a5a;
  cursor: not-allowed;
  transform: none;
}

.reset-button {
  background-color: var(--secondary-color);
  color: var(--text-color);
  border: none;
  padding: 12px 24px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
}

.reset-button:hover {
  background-color: #b4652a;
}

/* Creative Corner Story Input */
.story-text-container {
  margin-bottom: 20px;
}

.story-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.story-header label {
  color: var(--accent-color);
  font-weight: 500;
}

.import-controls {
  display: flex;
  gap: 10px;
}

.import-button {
  background-color: var(--bg-secondary);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.import-button:hover {
  background-color: var(--highlight-color);
}

.char-hint, .import-hint {
  font-size: 13px;
  color: var(--text-color);
  opacity: 0.7;
  margin-top: 10px;
}

/* Progress and Status Indicators */
.processing-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}

.loading-animation {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(94, 140, 49, 0.3);
  border-radius: 50%;
  border-top-color: var(--primary-color);
  animation: spin 1s ease-in-out infinite;
  margin-bottom: 15px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error-message {
  color: var(--error-color);
  padding: 10px;
  margin-bottom: 20px;
  background-color: rgba(215, 100, 100, 0.1);
  border-radius: 5px;
  text-align: center;
}

/* Footer */
footer {
  text-align: center;
  margin-top: 40px;
  padding: 20px 0;
  color: var(--text-color);
  opacity: 0.8;
  font-size: 14px;
}

/* Inspiration Box */
.inspiration-box {
  background-color: rgba(0, 0, 0, 0.15);
  padding: 20px;
  border-radius: 10px;
  margin-top: 30px;
  border-left: 4px solid var(--accent-color);
}

.inspiration-box h3 {
  color: var(--accent-color);
  margin-bottom: 10px;
  font-size: 1.3rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .App {
    padding: 15px;
  }
  
  .App-header h1 {
    font-size: 2rem;
  }
  
  .voice-controls {
    flex-direction: column;
  }
  
  .instruction-categories {
    grid-template-columns: 1fr;
  }
  
  .section-header {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }
  
  .button-group {
    flex-direction: column;
  }
}

/* Reading Rules Section */
.reading-rules-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.manage-rules-btn {
  background-color: var(--bg-secondary);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.manage-rules-btn:hover {
  background-color: var(--highlight-color);
}

.rules-hint {
  font-size: 13px;
  color: var(--text-color);
  opacity: 0.8;
  margin-top: 6px;
  text-align: center;
}

/* Wil Wheaton Toggle */
.wil-wheaton-toggle {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  background-color: rgba(204, 156, 58, 0.15);
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
}

.wil-toggle-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.wil-toggle-label input {
  margin: 0;
}

.toggle-text {
  font-weight: bold;
  color: var(--accent-color);
}

.wil-hint {
  font-size: 13px;
  color: var(--text-color);
  opacity: 0.8;
  margin-top: 6px;
  text-align: center;
}